import React from "react";
import ReactDOM from "react-dom";

const getTime = () => {
  return new Date().toLocaleTimeString();
};

const Time = () => {
  return (
    <div>
      <p>Current Time:</p>
      <h3>{getTime()} </h3>
    </div>
  );
};

ReactDOM.render(<Time />, document.querySelector("#root"));
